import React, { Component } from 'react';
import BaseVisualization from '@splunk/dashboard-visualizations/common/BaseVisualization';
import { truncateParsedData } from '@splunk/dashboard-visualizations/utils/dataUtils';
import { withVisualizationAndThemeHOCs } from '@splunk/dashboard-visualizations/utils/enhancer';
import { enterprise } from '@splunk/visualization-themes/variables';
import ReactScatter from '@splunk/react-visualizations/Scatter';
import { parse, dataContract } from '@splunk/visualization-encoding-parsers/ScatterParser';
import VisualizationEvent from '@splunk/dashboard-visualizations/common/VisualizationEvent';
import IconPlaceholder from '@splunk/dashboard-visualizations/common/IconPlaceholder';
import { Scatter as ScatterIcon } from '@splunk/visualization-icons';
import { getPropsForSimpleXMLOptions } from '@splunk/visualizations-shared/simpleXMLOptions';
import { enableClickHandler } from '@splunk/dashboard-visualizations/utils/eventUtils';
import { getChartingThemeKey } from '@splunk/dashboard-visualizations/utils/themeUtils';
import optionsSchema from './optionsSchema';
import editor from './editorConfig';

export class ScatterComponent extends Component {
    static propTypes = BaseVisualization.propTypes;

    static defaultProps = {
        ...BaseVisualization.defaultProps,
        height: 250,
    };

    static dataContract = dataContract;

    static vizContract = {
        initialDimension: {
            width: 300,
            height: 300,
        },
    };

    static editor = editor;

    static schema = optionsSchema;

    render() {
        const {
            dataSources,
            encoding,
            options,
            width,
            height,
            style,
            theme,
            mode,
            hasEventHandlers,
        } = this.props;
        const themeKey = getChartingThemeKey(theme);
        let dataProps = parse(dataSources, encoding);
        // todo: may need merge the truncateParsedData function with parse
        const truncationLimit = options && options['chart.resultTruncationLimit'];
        dataProps = truncateParsedData(dataProps, truncationLimit);

        const onPointClick = evt => {
            if (enableClickHandler(hasEventHandlers, mode)) {
                this.props.onEventTrigger(
                    new VisualizationEvent({
                        originalEvent: evt.originalEvent,
                        payload: evt.payload,
                        type: 'point.click',
                    })
                );
            }
        };

        const onLegendClick = evt => {
            if (enableClickHandler(hasEventHandlers, mode)) {
                this.props.onEventTrigger(
                    new VisualizationEvent({
                        originalEvent: evt.originalEvent,
                        payload: evt.payload,
                        type: 'legend.click',
                    })
                );
            }
        };

        return (
            <ReactScatter
                width={width}
                height={height}
                style={style}
                themeKey={themeKey}
                x={dataProps.x}
                y={dataProps.y}
                category={dataProps.category}
                xFieldName={dataProps.xFieldName}
                yFieldName={dataProps.yFieldName}
                categoryFieldName={dataProps.categoryFieldName}
                onPointClick={onPointClick}
                onLegendClick={onLegendClick}
                {...getPropsForSimpleXMLOptions(options)}
            />
        );
    }
}

export default withVisualizationAndThemeHOCs({
    defaultBackgroundColor: enterprise.defaultBackgroundColor,
    placeholder: <IconPlaceholder icon={<ScatterIcon />} />,
})(ScatterComponent);
